.product-page {
  display: flex;
  flex-direction: column;
}

.product-page-breadcrumb {
  padding: 4px;
  margin-bottom: 16px;
}

.product-page-breadcrumb .link {
  color: dimgray;
  padding: 0 8px;
}

.product-image {
  grid-area: image;
}

.product-info {
  grid-area: info;
}

article {
  display: grid;
  grid-gap: 1em;
  grid-template-areas:
    "image info"; 
}

.product-info > h1 {
  font-size: 3em;
}

.product-price {
  font-family: Helvetica, Arial, sans-serif;
  font-weight: 600;
  font-size: 2em;
  margin-bottom: 16px;
}

@media only screen and (min-width: 500px) {
  article {
    grid-template-areas:
      "image info"
  }
}

@media only screen and (max-width: 500px) {
  article {
    grid-template-areas:
      "image"
      "info"
  }

  .product-page-breadcrumb {
    display: none;
  }
}

